$myFont: 'Roboto', sans-serif;
$pri-color: #1E4077;
$secondry-color:#F39223;
$white:#fff;
$fontsize: 16px;
body {
  	font-family: $myFont;
	font-size: $fontsize
}
.ragister-login{
  overflow:hidden;
  .row{
  	  height:100vh !important;
	  background-image: url(../images/left-bg.jpg);
	  background-size: 41.66%;
	  background-repeat: no-repeat;
  }
	#sliderlogin{
	  padding: 60px 95px;
	}
	#sliderlogin h3{
	  font-weight: 300;
	  font-size: 38px;
	  line-height: 50px;
	  text-align: center;
	  color: #FFFFFF;
	}
	.carousel-indicators [data-bs-target]{
		width: 12px;
    	height: 12px;
		border-radius: 50%;
		opacity: 1;
	}
	.carousel-indicators .active{
		    background-color: $secondry-color;
	}
}
.bottomcopyright{
	  overflow:hidden;
	  position:absolute;
	  width:100%;
	  bottom: 15px;
	p{
	   margin-bottom:0;
	  font-weight: normal;
	  font-size: 14px;
	  line-height: 16px;
	  text-align: center;
	  color: #FFFFFF;
	}
}

.topheader{
		display:flex;
		padding: 50px;
		
		.left-logo{
			width:50%;
		}
		.right-sign{
			width:50%;
			text-align:right;
		a{
			@extend .button-box;
		}
	}
}
.topheader-section{
	position: absolute;
    width: 100%;
    top: 0;
	overflow:hidden;
}
.button-box{
	border: 1px solid $pri-color;
	box-sizing: border-box;
	border-radius: 5px;
	color: $pri-color;
	font-size: 18px;
	line-height: 21px;
	font-family: $myFont;
	text-decoration: none;
    padding: 12px 25px;
}
.form-body{
    padding: 150px; 
	padding-top:150px;
	h1{
		font-weight: bold;
		font-size: 50px;
		line-height: 59px;
		color: #1E4077;
		text-align:center;
		margin-bottom:30px
	}
	input{
		border: 1px solid #DAD9D9;
		box-sizing: border-box;
		border-radius: 5px;
		padding:15px;
		width:100%;
		margin-bottom:15px;
		font-size: 18px;
		line-height: 21px;
		color: #000;
		font-family: $myFont;
		::placeholder{
			font-size: 18px;
			line-height: 21px;
			color: #A4A4A4;
			font-family: $myFont;
		}
	}
	input[type="submit"]{
		background-color: $secondry-color;
		width:auto;
		padding:15px 50px;
		border:1px solid $secondry-color;
		color:$white;
		
	}
	a{
		@extend .button-box;
		
	}
}
.logindiv{
	input{
		display:inline-block;
		width:40%
	}
	span{
		display:inline-block;
		width: 17%;
		font-weight: normal;
		font-size: 18px;
		line-height: 30px;
		color: #A4A4A4;
		text-align: center;
	}
	.google-div{
		display:inline-block;
		width:40%;
		text-align: right;
		a{
			text-align: center;
			color: #518EF8;
			border: 1px solid #518EF8;
			letter-spacing:0;
			 img{
				margin-right:7px
			}
		}
	}
}
.loginpage{
	.row{
  	   background-image: url(../images/left-login.jpg);
	}
}